<template>
  <div>
    <h3 v-show="!isCollapse">Element UI 后台管理</h3>
    <h3 v-show="isCollapse" style="text">后台</h3>

    <!-- <el-collapse-transition> -->
    <el-menu class="el-menu-vertical-demo" :collapse="isCollapse">
      <el-menu-item
        v-for="(item, i) in noChildren"
        :index="item.path"
        :key="item.path"
        @click="clickMenu(item.path)"
      >
        <i :class="item.icon"></i>
        <span slot="title">{{ item.name }}</span>
      </el-menu-item>

      <el-submenu
        v-for="(item, i) in hasChildren"
        :index="item.path"
        :key="item.path"
      >
        <template slot="title">
          <i class="el-icon-location"></i>
          <span slot="title">{{ item.name }}</span>
        </template>

        <el-menu-item-group>
          <el-menu-item
            v-for="(val, key) in item.children"
            :index="val.path"
            :key="key"
            >{{ val.name }}
          </el-menu-item>
        </el-menu-item-group>
      </el-submenu>
    </el-menu>
    <!-- </el-collapse-transition> -->
  </div>
</template>

<script>
export default {
  data() {
    return {
      menu_list: [
        {
          name: "首页",
          path: "/",
          icon: "el-icon-menu",
        },
        {
          name: "商品管理",
          path: "/Goods",
          icon: "el-icon-goods",
        },
        {
          name: "用户管理",
          path: "/User",
          icon: "el-icon-document",
        },

        {
          name: "设置",
          path: "/setting",
          icon: "el-icon-setting",
          children: [
            {
              name: "设置1",
              path: "/setting/set1",
              icon: "el-icon-setting",
            },
            {
              name: "设置2",
              path: "/setting/set2",
              icon: "el-icon-setting",
            },
          ],
        },
      ],
    };
  },
  computed: {
    isCollapse() {
      return this.$store.state.isCollapse;
    },
    noChildren() {
      return this.menu_list.filter((item) => item.children == null);
    },

    hasChildren() {
      return this.menu_list.filter((item) => item.children != null);
    },
  },

  methods: {
    clickMenu(path) {
      this.$router.push(path);
    },
  },
};
</script>

<style>
.el-menu-vertical-demo:not(.el-menu--collapse) {
  width: 200px;
  min-height: 400px;
}
</style>