import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    isCollapse:false
  },
  mutations: {
    handleIsCollapse(state){
      // console.log(state.isCollapse);
      state.isCollapse =  !state.isCollapse
    }
  },
  actions: {
    handleIsCollapse (context) {
      // console.log('sss');
      context.commit('handleIsCollapse')
    }
  },
  modules: {
  }
})
