<template>
  <def :option="option" />
</template>

<script>
import def from "./def";
require("echarts/lib/chart/bar"); //按需引入 bar = 柱状图
export default {
  props: {
    option: {
      type: Object,
      default: () => {
        return {
          xAxis: {
            type: "category",
            data: ["Mon", "Tue", "Wed", "Thu", "Fri", "Sat", "Sun"],
          }, //X轴
          yAxis: { type: "value" }, //Y轴
          series: [{ data: [120, 200, 150, 80, 70, 110, 130], type: "bar" }], //配置项
        };
      },
    },
  },
  components: { def },
};
</script>

