<template>
  <el-breadcrumb separator="/">
    <el-breadcrumb-item :to="{ path: '/' }">首页 </el-breadcrumb-item>
    <el-breadcrumb-item>{{ pathName }}</el-breadcrumb-item>
  </el-breadcrumb>
</template>

<script>
export default {
  data() {
    return {
      pathArr: { "/User": "用户管理", "/Goods": "商品管理" },
    };
  },
  computed: {
    pathName() {
      return this.pathArr[this.$route.path];
    },
  },
};
</script>

